body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
  background: -webkit-linear-gradient(left, rgba(105, 187, 28, 0.9), rgba(4, 218, 218, 0.9));
  background: linear-gradient(to right, rgba(105, 187, 28, 0.9), rgba(4, 218, 218, 0.9));
}

a {
  color: #333;
  text-decoration: none;
}

.container{
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: 500px;
}

.answer-container{
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: 60%;
  margin: auto;
}

.loader:empty {
  position: absolute;
  top: calc(50% - 4em);
  left: calc(50% - 4em);
  width: 6em;
  height: 6em;
  border: 1.1em solid rgba(0, 0, 0, 0.2);
  border-left: 1.1em solid #000000;
  border-radius: 50%;
  -webkit-animation: load8 1.1s infinite linear;
          animation: load8 1.1s infinite linear;
}

@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.input-field{
  padding: 6px;
  border: 1px #ccc solid;
  border-radius: 5px;
  width: 100%;
  margin-top: 5px;
}

.input-field:focus{
  box-shadow: 0 0 0 1px rgba(63, 221, 15, 0.3) !important;
}

.submit-button{
  width:90%;
  margin-left: 5%;
  border:none;
  padding: 10px;
  border-radius: 20px;
  background: rgba(105, 187, 28, 0.9);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.submit-button:hover{
  background: rgb(11, 197, 197);
}

.form-shown {
  opacity: 1;
  -webkit-transition: all 250ms linear;
  transition: all 250ms linear;
}

.form-hidden {
  opacity: 0;
  -webkit-transition: all 250ms linear;
  transition: all 250ms linear;
  display: none;
}

.quiz-heading{
  text-transform: capitalize;
}

@media screen and (max-width: 560px) {
  .container{
    width: 100%;
  }

  .answer-container{
    width: 100%;
  } 
}

